<template>
  <div class="idMsg">
    <loading v-show="isLoading"/>
    <van-nav-bar title="账号管理" left-text="返回" @click-left="goBack" />
    <van-cell title="头像" class="photo">
      <div class="img-box fr">
        <img class="v-img" :src="userData.userImg" @oversize="oversize"/>
        <van-uploader :max-size="maxSize" :after-read="afterRead" class="up-img" @oversize="oversize" />
      </div>
    </van-cell>
    <van-cell class="nickName" title="昵称">
      <input type="text" v-model="userData.nickName" @blur="blur({url:'/updateNickName',key:'nickName',value:userData.nickName})" />
    </van-cell>
    <van-cell title="用户 ID" :value="userData.userId" />
    <van-cell title="手机号" :value="userData.phone" />
    <van-cell title="简介" class="v-box">
      <textarea rows="2" maxlength="35" class="v-text" v-model="userData.desc" @blur="blur({url:'/updateDesc',key:'desc',value:userData.desc})"></textarea>
    </van-cell>
  </div>
</template>

<script>
import "../assets/css/idMsg.less";
import loading from "../components/loading";
export default {
  components:{loading},
  data() {
    return {
      isLoading:false,
      userData: {},
    //  头像最大上传大小
      maxSize: 1024 * 500
    };
  },
  created() {
    this.getUserData();
  },
  methods: {
    goBack() {
      this.$router.go(-1);
    },
    // 上传头像
    afterRead(file) {
      this.isLoading = true;
      // 此时可以自行将文件上传至服务器
      // 获取base64格式图片
      let imgData = file.content.split(",")[1];
      let type = file.file.type.split("/")[1];
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: "/updateAvatar",
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          imgType: type,
          serverBase64Img: imgData
        }
      })
        .then(result => {
          if (result.data.code == 'H001') {
            this.userData.userImg = result.data.userImg;
          } else {
            this.$toast(result.data.msg);
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });

    },
    // 获取用户数据
    getUserData() {
      this.isLoading = true;
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "get",
        url: "/findAccountInfo",
        params: {
          appkey: this.appkey,
          tokenString: tkStr
        }
      })
        .then(result => {
          if (result.data.code == "B001") {
            this.userData = result.data.result[0];
          }
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 修改昵称 / 简介
    blur(obj) {
      let tkStr = localStorage.getItem("__Tk");
      this.axios({
        method: "post",
        url: obj.url,
        data: {
          appkey: this.appkey,
          tokenString: tkStr,
          [obj.key]: obj.value
        }
      })
        .then(result => {
          this.isLoading = false;
        })
        .catch(err => {
          this.isLoading = false;
        });
    },
    // 上传图片超出大小提示
    oversize() {
      this.$toast("图片大小不能超过500k哦");
    }
  }
};
</script>
